<template>
  <div class="login">
    <!-- 登录 -->
    <div>
      <div class="back" @click="back">
        <van-icon name="arrow-left" />
      </div>
      <div class="title">豆果美食</div>
      <van-form>
        <van-field
          v-model="account"
          name="手机号"
          label="手机号"
          placeholder="请输入手机号"
          autocomplete="off"
          :rules="[{ required: true, message: '请填写手机号' }]"
        />
        <van-field
          v-model="password"
          :type="!isOpenLogin ? 'password' : 'text'"
          name="密码"
          label="密码"
          placeholder="密码"
          :right-icon="!isOpenLogin ? 'closed-eye' : 'eye-o'"
          @click-right-icon="toggleLoginPasssword"
          autocomplete="off"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 16px;">
          <van-button class="btnd" round block type="info" native-type="submit" @click="getstorager">提交</van-button>
        </div>
        <div style="margin: 16px;">
          <van-button round block type="button" class="register" @click="register">注册</van-button>
        </div>
      </van-form>
    </div>
    
    
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  name: "Login",
  data() {
    return {
      
      isOpenLogin: false,
      name: '',
      account: '',
      password: '',

      list: [{
        title: '我的收藏'
      },{
        title: '消息中心'
      }]
    };
  },
  
  methods: {
      
    //注册
     register() {
       this.$router.push({name: 'Register'});
     },

    //返回
     back() {
       this.$router.push({name: 'Home'});
     },


    //登录
     getstorager() {
       
       let userInfo = localStorage.getItem('data');
       
       if (this.account === '' || this.password === '') {
         Toast('账号或者密码不能为空')
         return
       }
       if (userInfo === null) {
         Toast('账号没注册')
         return;
       }
       let userInfoObj = JSON.parse(userInfo);
       
       if (this.account != userInfoObj.account || this.password != userInfoObj.password) {
         Toast('账号或密码输入错误');
         this.account = '';
         this.password = '';
         return;
       }

       if (this.account === userInfoObj.account && this.password === userInfoObj.password) {
         Toast('登录成功');
         this.$router.push({name: 'Home'});
         this.$root.isLogin = true;
       }
     },

    toggleLoginPasssword() {
      this.isOpenLogin = !this.isOpenLogin;
    },
     
  }
};
</script>

<style lang="less" scoped>
  .login {
    .back {
      padding: 10px;
      width: 20px;
      height: 20px;
    }
    .title {
      padding: 20px;
      font-size: 30px;
      font-weight: bold;
      margin-top: 150px;
    }
    .register {
      display: block;
      border: none;
    }
  }
  .btnd{
    background-color: #facc30;
  }

  /deep/.van-button--info{
    border: #facc30;
  }
</style>
